@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100&family=Kufam:wght@600&family=Mulish&display=swap');


$font-1: 'Mulish', cursive;
$font-2: 'Kufam', sans-serif;
$font-3: 'Josefin Sans', serif;


$color-1: #2a1d52;
$color-2: #6C77F4;
$color-3: #63a4e9;
$color-4: #4FEDD2;
$color-5: #36D0EA;
$white: #ddd;
$black: #333;
$green: #d2f8d2;

$borderRadius: 25px;
$shadow: 0 1px 3px #333;
$minHeight: 95vh;

@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {  
  // background-image: linear-gradient(to right bottom, #9CECFB, #65C7F7, #0052D4);
  // background-size: cover;
  // background-repeat: no-repeat;
  background: $green;
  margin: 1rem auto;
  max-width: 1200px;
  font-family: $font-1;
  color: $black;
}

button {
  cursor: pointer;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color: $black;
  cursor: pointer;
}

a:visited {
  color: $black;
}

.home, .skills, .project, .contact {
  display: flex;

  @media screen and (max-width: 850px) {
    display: block !important;
  }
}

.homeContent, .skillsContent, .projectContent, .contactContent {
  margin-left: .4rem;

  @media screen and (max-width: 850px) {
    margin-left: 0 !important;
  }
}

