.skills {
  .skillsContent {
    min-height: $minHeight; 
    width: 100%;
    position: relative;
    overflow: hidden; //hide everything that exceeds
    display: grid;
    grid-template-columns: 38% 1fr 1fr;
    grid-template-rows: 70% 30%;
    grid-template-areas: 
      "Lan Exp Exp" 
      "Int Int Hob";

    .languagesNframeworks, .experience, .otherSkills, .hobbies {
      background: $color-2;
      border-radius: $borderRadius;
      padding: .8rem;
    }

    h3 {
      font-size: 1.4rem;
      margin-bottom: .3rem;
      font-family: $font-2;
      color: $color-5;
      text-transform: uppercase;
    }

    .languagesNframeworks {
      grid-area: Lan;
      margin-bottom: .5rem;
      display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr;
      grid-template-areas: 
        "Lang" 
        "Fram";

      .languagesDisplay {
        grid-area: Lang;
      }
      .frameworksDisplay {
        grid-area: Fram;
      }
      .years {
        font-size: .7rem;
        display: flex;
        justify-content: space-between;
        color: $white;

        span {
          width: 36%;

          &:nth-child(2) {
            text-align: center;
            width: 20%;
          }

          &:nth-child(3) {
            text-align: right;
            width: 44%;
          }

          &:nth-child(2):before, &:nth-child(3):before {
            content: '';
            background: rgba(180, 179, 179, .5);
            position: absolute; //to put it where I want
            height: 146px;
            width: 1px;
            transform: translate(20px, 19px);
          }
        }
      }

      .languagesList {
        padding-top: .5rem;

        li {
          color: $color-1;
        }
        .progressBar {
          height: .35rem;
          background: transparent;
          border-radius: 50px;
          margin-top: .2rem;
          position: relative;

          &:after {
            content: '';
            background: yellowgreen;
            position: absolute;
            border-radius: 50px;
            height: .35rem;
            width: 0;
            animation: progressbar 2s ease-out forwards;
            

            @keyframes progressbar {
              to {
                width: 100%;
              }
            }
          }
        }
      }
    }

    .experience {
      grid-area: Exp;
      margin: 0 0 .5rem .5rem;
      display: grid;
      grid-template-columns: 1fr;
      // grid-template-rows: 10% 1fr 1fr;
      grid-template-rows: 10% 1fr 1fr 1fr;
      grid-template-areas: 
        "1"
        "2"
        "3"
        "4";
      
      h3 {
        grid-area: 1;
      }
      h4 {
        font-size: 1.1rem;
        color: $color-5;
      }
      h5 {
        color: $color-5;
        padding: 0.5rem 0 0.5rem 1rem;
      }
      p {
        text-align: justify;
        padding: 0 1rem;
      }
      .exp-1 {
        grid-area: 2;
      }
      .exp-2 {
        grid-area: 3;
      }
      .exp-3 {
        grid-area: 4;
      }

      .experienceContent {
        padding: 0 0 0 1.5rem;

        ul {
          list-style-type: square;
          
          li {
            padding-bottom: .5rem;
          }
        }
      }
    }

    .otherSkills {
      grid-area: Int;
      margin-right: 0.5rem;

      .list {
        display: flex;
        width: 80%;
        height: 100%;
        // margin: 0 auto;
        margin: 1rem auto 0;
        justify-content: space-between;

        ul {
          height: 80%;
          display: grid;

          i {
            color: $color-5;
          }
        }
      }
    }
    
    .hobbies {
      grid-area: Hob;
      padding-left: 1.5rem;

      ul {
        height: 80%;
        display: grid;
        margin-top: 1rem;

        li {
          i {
            position: absolute;
            color: $color-5;
          }
          span {
            position: absolute;
            transform: translateX(2.2rem);
          }
        }
      }
    }
  }
}


@media screen and (max-width: 950px){
  .skillsContent {
    h3 {
      font-size: 1.2rem !important;
    }
    .languagesList {
      padding-top: .2rem !important;
    }
    p {
      font-size: .8rem;
    }
    .otherSkills, .hobbies {
      font-size: .8rem;
    }
  }
}

@media screen and (max-width: 850px){
  .skillsContent {
    h4 {
      font-size: .9rem !important;
    }
    h5 {
      font-size: .6rem !important;
      padding: .2rem 1rem !important;
    }
  }
}

@media screen and (max-width: 500px){
  .skillsContent {
    display: grid !important;
    grid-template-columns: 1fr !important;
    grid-template-rows: 1fr 1fr 180px 180px !important;
    grid-template-areas: 
      "1"
      "2"
      "3"
      "4" !important;
    
    .languagesNframeworks {
      grid-area: 1 !important;
    }
    .experience {
      grid-area: 2 !important;
      margin-left: 0 !important;
    }
    .otherSkills {
      grid-area: 3 !important;
      margin-right: 0 !important;
    }
    .hobbies {
      grid-area: 4 !important;
      margin-top: .5rem !important;
    }
  }
}
    
 